import React, {Component } from 'react';
import { Link } from 'react-router-dom';
import './body.css';



const Nav = () => {
  return (
      <ul className="nav abs text-monospace">
          <li className="nav-item">
              <Link className="nav-link" to="/">Home</Link>
          </li>
          <li className="nav-item">
              <Link className="nav-link" to="/nnoxo">Nnoxo</Link>
          </li>
          <li className="nav-item">
              <Link className="nav-link" to="/preview">Timer</Link>
          </li>
          <li className="nav-item">
              <Link className="nav-link" to="/todo">Todo</Link>
          </li> 
          <li className="nav-item">
              <Link className="nav-link" to="/burger">Burger</Link>
          </li> 
          <li className="nav-item">
              <Link className="nav-link" to="/rema">Dimension</Link>
          </li> 
  </ul>
 


  );
}




class Header extends Component {
    render() {

        return (
                <Nav />
        );
    }
}






export default Header;
