import React from 'react';
import {Containered, RemaInnerDiv, RemaButton, RemaOverlay, RemaBottom, RemaTop, RemaText, RemaTextFlipped, Remalink} from './components'; 
import { useState, useEffect } from 'react';
import rema1 from '../images/rema/remabg1.png';
import rema2 from '../images/rema/remabg2.png';
import rema3 from '../images/rema/remabg3.png';
import rema4 from '../images/rema/remabg4.png';
import top1 from '../images/rema/rematop1.png';
import top2 from '../images/rema/rematop2.png';
import top3 from '../images/rema/rematop3.png';
import top4 from '../images/rema/rematop4.png';
import bot1 from '../images/rema/remabot1.png';
import bot2 from '../images/rema/remabot2.png';
import bot3 from '../images/rema/remabot3.png';
import bot4 from '../images/rema/remabot4.png';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faStepForward } from '@fortawesome/free-solid-svg-icons'
import { motion } from "framer-motion";



import useEventListener from '@use-it/event-listener'
//import {Container, Row, Col} from 'react-bootstrap';

const Slider = () => {
    //let ReactCSSTransitionGroup = require('react-addons-css-transition-group');
    let [imgs] = useState([{bg: rema1, top: top1, bot: bot1}, {bg: rema2, top: top2, bot: bot2}, {bg: rema3, top: top3, bot: bot3}, {bg: rema4, top: top4, bot: bot4}]);
    let [index , changeIndex] = useState(0);
    let value = imgs[index];
    let [pulseOnce, setPulseOnce] = useState(false)

    /*const changeIndex = () =>{
        changeIndex
    }
    <ReactCSSTransitionGroup transitionName = "example"
                    transitionAppear = {true} transitionAppearTimeout = {500}
                    transitionEnter = {false} transitionLeave = {false}>
                            
                    <h1>My Element...</h1>
                </ReactCSSTransitionGroup>
    
    */

    const variants = {
        0: { opacity: 1, x: 0 },
        1: { opacity: 0, x: "-100%" },
        2: { opacity: 0, x: "-100%" },
        3: { opacity: 0, x: "-100%" },
        }

    useEffect(() => {
        
      }, [value, index, pulseOnce]);
      //console.log(value, value.bg);
      const Check = () => {
          if(pulseOnce == false){
              return(
                <div>
                    <motion.div
                        initial = {{x: -1200, y: 450}}
                        animate={{ x: -800}}
                        transition={{delay: .1, type: 'spring', stiffness: 10}}
                        >
                        <RemaBottom background={value, value.bot}></RemaBottom>   
                    </motion.div>

                    <motion.div
                    initial = {{x: 1200, y: -250}}
                    animate={{ x: 700}}
                    transition={{delay: .1, type: 'spring', stiffness: 10}}
                    >
                    <RemaTop background={value, value.top}></RemaTop>
                    </motion.div>
                </div>
              )
          }
      }
      
      const handleSubmit = () => {
          if (index >= 3) {
            changeIndex(index = 0)
          }else{
            changeIndex(index + 1)
          }
       
      }

      /*{imgs.map(item => (
        <div key={item.id}>{item.bg}</div>            
      ))}*/
      return (
        <RemaInnerDiv background={value, value.bg}>
            <RemaOverlay>
            <Check /> 
            <motion.div
                initial = {{y: -50, opacity: 0}}
                animate={{ y: 1, opacity: 1}}
                transition={{delay: .1, type: 'spring', stiffness: 10}}
                >
                <RemaText>DIMENSION</RemaText>  
            </motion.div>
            <motion.div
                initial = {{y: 50, opacity: 0}}
                animate={{ y: 1, opacity: 1}}
                transition={{delay: .1, type: 'spring', stiffness: 10}}
                >
                <RemaTextFlipped>DIMENSION</RemaTextFlipped> 
            </motion.div>
                
                
                <RemaButton onClick={handleSubmit}><FontAwesomeIcon icon={faStepForward} /> </RemaButton>    
                <Remalink>WATCH ON YOUTUBE</Remalink>  
                      
            </RemaOverlay>             
                            
        </RemaInnerDiv> 
      );
  }

  /*Object.keys(props.data).map((obj, i) => {
    return (
      <div>
        {props.data[obj].name}
      </div>
    )})}

    {imgs.map(arrVal => (
        <li key={arrVal}>{arrVal}</li>
    ))}
    
    {imgs.arrOfStrings.map((item, i) => {
        return <li key={i}>{item}</li>
      })}
    
    */



const Rema = () => {
    let [imgs, ChangeImg] = useState([{bg: "bg1"}, {bg: "bg1"}, {bg: "bg1"}]);
    
    


    return (
        <>
            <Containered>
                <Slider />                
            </Containered>
            
            

        </>           
    
    );
};

export default Rema;

