//import ReactDOM from 'react-dom'
import React, { useState, useEffect } from 'react';
import {Containered, TimerDivBug, InputNumBug, Button, CountDiv, Count, Text} from './components';

let num = 0;
function Preview(){
    let [count, setCount] = useState(0);
    let [mins, setMins] = useState(0);
    //const [secs, setSecs] = useState(0);
    let [startCounter, setStartCounter] = useState(false);
    let [text, setText] = useState('');

    const calTime = () => {
      if(count >= 60){
        setCount(count - 60);
        setMins(mins + 1);
      }
    }
    
    const counter = () => {
        
        

        /*if(count < 60){
          setStartCounter(startCounter);
          console.log(startCounter);
        }*/
        //console.log(startCounter)

        if(startCounter){
          if(count > 0){
            setCount(count-1);
          }
          
          if(count <= 0 ){
            if(mins <= 0  && count <= 0){
              //console.log('time up');
              setText(text = "TIME'S UP");
              setStartCounter(!startCounter);              
            }
            
            if(mins > 0){
              setMins(mins - 1);
              setCount(count + 59);
            }            
          }

          
        }

        //console.log(secs);
    }


    useEffect(() => {
      const interval = setInterval(() => {
             counter();
      }, 1000);
      calTime();     
      return () => clearInterval(interval);       
    }, [count, mins, startCounter]);

    let textInput = React.createRef();
    const handleSubmit = () => {
      setMins(mins = 0);
      setText(text = "");
      setCount(count = textInput.current.value);
      setStartCounter(startCounter = true);
    }

  return (
    <>
      <Containered> 
        <TimerDivBug>
          <InputNumBug ref ={textInput} type="number" className="countVal" placeholder="SECONDS" />
          <Button onClick={handleSubmit} >SUBMIT</Button>
          <CountDiv>
            <Count>{mins} : {count}</Count>
            <Text>{text}</Text>
          </CountDiv>
             
        </TimerDivBug>    
            
      </Containered>
    </>
  )

}



