import styled from 'styled-components';
import { createGlobalStyle } from 'styled-components';


//import Funzone2Outline from './fonts/funzone/Funzone2Outline.tff';

import Funzone2Outline from './Funzone2Outline.ttf';
//import NameOfYourFontWoff2 from './nameOfYourFont.woff2';

export default createGlobalStyle`
    @font-face {
        font-family: 'Funzone';
        src: local('Funzone'), local('Funzone'),
        url(${Funzone2Outline}) format('tff');
        font-weight: 300;
        font-style: normal;
    }
`;