import React, { useState, useEffect } from 'react';
//import ReactDOM from 'react-dom';
import {Containered, TimerDivBug, ButtonBug, ButtonBugS, CountDivBug, Text, LI} from './components';

const Burger = () => {
    let [arr, setArr] = useState([]);
    //let [arr2, setArr2] = useState([]);
    let [num, setnum] = useState(0); 
    let [height, setheight] = useState('15px'); 
    let [target, setTarget] = useState(true);
    
    //<img src={'../images/samuria.jpg'} /> 

    //let Bun = <Text>yooo</Text>; 
    //let ReactNodeLi = React.createElement('li', {id:'li1'}, 'one');

    
    function Reverse(array){
        return array.map((item,idx) => array[array.length-1-idx])
      }
    
    useEffect(() => {
        console.log(num);  
      }, [arr, num]);
  
      //let textInput = React.createRef();
      const handleSubmit = (e) => {
          if(e == 'bunb'){
            setArr(arr.concat(<img src={require('../images/images/bunb.png')} className="img" style={{ position: 'relative', zIndex: num }} />));
          }else if(e == 'egg'){
            setArr(arr.concat(<img src={require('../images/images/egg.png')} className="img" style={{ position: 'relative', zIndex: num }} />));
          }else if(e == 'bunt'){
            setArr(arr.concat(<img src={require('../images/images/bunt.png')} className="img" style={{ position: 'relative', zIndex: num }} />));
          }else if(e == 'meat'){
            setArr(arr.concat(<img src={require('../images/images/meat.png')} className="img" style={{ position: 'relative', zIndex: num }} />));
          }else if(e == 'chicken'){
            setArr(arr.concat(<img src={require('../images/images/chicken.png')} className="img" style={{ position: 'relative', zIndex: num }} />));
          }else if(e == 'cheese'){
            setArr(arr.concat(<img src={require('../images/images/cheese1.png')} className="img" style={{ position: 'relative', zIndex: num }} />));
          }else if(e == 'tomato'){
            setArr(arr.concat(<img src={require('../images/images/tomato.png')} className="img" style={{ position: 'relative', width:'15%', zIndex: num }} />));
          }else if(e == 'split'){
            setTarget(!target);
            target ? setheight('70px') : setheight('15px');
          }     
          
          setnum(num + 1);
          
   
      }
      let revArr = Reverse(arr);

      


    return (
        
        <>
            
            <Containered> 
                <TimerDivBug>
                    <ButtonBug onClick={() => handleSubmit('bunb')} >Burger Bun Bottom</ButtonBug>
                    <ButtonBug onClick={() => handleSubmit('egg')} >Egg</ButtonBug>
                    <ButtonBug onClick={() => handleSubmit('meat')} >Burger Meat</ButtonBug>
                    <ButtonBug onClick={() => handleSubmit('chicken')} >Burger Chicken</ButtonBug>
                    <ButtonBug onClick={() => handleSubmit('cheese')} >cheese</ButtonBug>
                    <ButtonBug onClick={() => handleSubmit('tomato')} >tomato</ButtonBug>
                    <ButtonBug onClick={() => handleSubmit('bunt')} >Burger Bun Top</ButtonBug>
                    <ButtonBugS onClick={() => handleSubmit('split')} >Split</ButtonBugS>
                    <CountDivBug>
                        <Text>
                            <ol className="ul">
                                {revArr.map((arrVal, i) => (                                    
                                <LI height={height} key={i}>{arrVal}</LI>                                     
                                ))}
                            </ol>
                        </Text>
                    </CountDivBug>
                    
             
                </TimerDivBug>    
            
      </Containered>
        </>

        
    )

    
}

export default Burger

