import React, { useState, useEffect } from 'react';
import {Containered, TimerDivBug, InputNumBug, Button, CountDiv, Text} from './components';

const Todo = () => {
    let [arr, setArr] = useState([]);
    let [line, setLine] = useState('none'); 

    /*const checkArr =   <ol>
                            {arr.map(arrVal => (
                                <li key={arrVal}>{arrVal}</li>
                            ))}
                        </ol>;*/
    

    useEffect(() => {
            
      }, [arr]);
  
      let textInput = React.createRef();
      const handleSubmit = () => {
        setArr(arr.concat(textInput.current.value));
      }

      /*const handleLine = () => {
            setLine(line = 'line-through');
      }

      let btn = 
      <ButtonBugS onClick={() => handleLine()}>X</ButtonBugS>
      ;*/


    return (
        
        <>
            
            <Containered> 
                <TimerDivBug>
                    <InputNumBug ref ={textInput} type="text" className="countVal" placeholder="Items" />
                    <Button onClick={handleSubmit} >SUBMIT</Button>
                    <CountDiv>
                        <Text>
                                <ol>
                                    {arr.map(arrVal => (
                                        <li style={{ textDecoration: line, textDecorationLine: 'blue'}} key={arrVal} className="TextDived">{arrVal}</li>
                                    ))}
                                </ol>
                        </Text>
                    </CountDiv>
             
                </TimerDivBug>    
            
      </Containered>
        </>
    )
}

export default Todo
