import React, { Component } from 'react';
//import logo from './logo.svg';
import './App.css';
import GlobalFonts from './fonts/font';
import Preview from './testfiles/preview';
import Nnoxo from './testfiles/Nnoxo';
import Index from './testfiles/Index';
import Todo from './testfiles/Todo';
import Burger from './testfiles/Burger';
import Rema from './testfiles/Rema';
import {Switch, Route} from 'react-router-dom';
import Header from './testfiles/header';



const Main = () => {
	return (

		      <Switch>
            <Route exact path="/"><Index /></Route>
            <Route path="/nnoxo" ><Nnoxo /></Route>
            <Route path="/preview" ><Preview /></Route>
            <Route path="/todo" ><Todo /></Route>
            <Route path="/burger" ><Burger /></Route>
            <Route path="/rema" ><Rema /></Route>
          </Switch> 
	);
}


function App() {
  return (
    <div className='App'>
      <GlobalFonts /> 
        <Main />
        <Header />       
                
    </div>
  );
}

export default App;


