import styled from 'styled-components';
import {FaTimes} from 'react-icons/fa';
import {CgMenuRightAlt} from 'react-icons/cg';
import img from '../images/yang_half.png';
import img1 from '../images/yin_half.png';
import img2 from '../images/samuria.jpg';
import img3 from '../images/absbg.jpg';
import {createGlobalStyle} from "styled-components";
import Funzone2Outline from '../fonts/Funzone2Outline.ttf'; 
/*import { injectGlobal } from "styled-components";

injectGlobal`
  @import url('https://fonts.googleapis.com/css?family=Notable');
  body {
    
  }
`*/

export const GlobalStyle = createGlobalStyle`
@import url('../fonts/Funzone2Outline.ttf');
@import url('https://fonts.googleapis.com/css?family=Notable');
    body{
        color: ${props => (props.Color ? 'red' : 'green')};
        font-family: 'Notable', sans-serif;
    }

    @font-face {
        font-family: Funzone;
        font-style: normal;
        font-weight: 400;
        src: url(${Funzone2Outline});
      }
    
`;




export const Containered = styled.div`
    padding: 2%;
    background: #111;
    positon:absolute;
    bottom:0;
    right:0;
    top: 0;
    left: 0;
    min-height: 100%;
    height:100vh;
    max-height: 100%;
    width:100%;
`;



export const InnerDiv = styled.div`
    width: 100%;
    height: 100%;
    padding: 5%;
    margin: 0;
    //border: 1px solid #BD1F2D;
    background: #111;
    //background-image: ${({display}) => ('url('+display+')')};
    background-image: url(${img2});
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    //transition: ${({transition}) => (transition)};
    //opacity: ${({opac}) => (opac)};

    /*&:hover {
        background-image: url(${img1})
    }*/
  

`;

export const BgFade = styled.div`
    padding: 0;
    margin: 0;
    //border: 3px solid red;
    background: #BD1F2D;
    position: absolute;
    width:100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 3;
    visibility: ${({openVis}) => (openVis)};
    transition: ${({transition}) => (transition)};
    opacity: ${({opac}) => (opac)};
`;


export const TestDiv = styled.div`
    width: ${({width}) => (width)};
    height: ${({height}) => (height)};
    //background: ${({ isColor }) => (isColor ? 'green' : 'red')}; 
    //background: ${({isColor}) => (isColor)};
    background-image: url(${img});
    //display:flex;
    color: red;
    transform: ${({ isOpen }) => ( 'rotate('+isOpen+'deg)')};
    -webkit-transition: 1s ease-in-out;
    -moz-transition: 1s ease-in-out;
    -o-transition: 1s ease-in-out;
    transition: 1s ease-in-out;
    position: absolute;
    z-index: 999;
    left: ${({left}) => (left)};
    right: 0;
    top: ${({top}) => (top)};
    bottom: 0;
    margin: auto;
    //border:1px solid red;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    text-align: center;
    //visibility: ${({openVis}) => (openVis)};
    //transition: ${({transition}) => (transition)};
    //opacity: ${({opac}) => (opac)};


`;

